//抓文章資料
let chat = [];
export function catch_chat(){
  onSnapshot(collection(db, "Chatroom"), (querySnapshot) => {
    chat = [];
    querySnapshot.forEach((doc) => {
      const Adddata = {
        Content:doc.data().Content,
        Writer:doc.data().Writer,
        Time:doc.data().Time,
        Id:doc.id
      }
      chat.push(Adddata);
    })
  })
  return chat;
}

//抓文章資料
let AData = [];
export function catch_data(){
  onSnapshot(collection(db, "Article"), (querySnapshot) => {
    AData = [];
    querySnapshot.forEach((doc) => {
      const Adddata = {
        Title:doc.data().Title,
        Content:doc.data().Content,
        Writer:doc.data().Writer,
        Time:doc.data().Time,
        Id:doc.id
      }
      AData.push(Adddata);
    })
  })
  return AData;
}
//刪除文章資料
export function delet_id(doc_id){
  deleteDoc(doc(db, "Article", doc_id));
}
