<script>
import {catch_data} from '../db'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },data(){
    return{
      enddata:[],
      loading_let:true,
      beforedata:[],
    }
  },
  methods:{
    catch_d(){
      this.beforedata=catch_data();
      this.beforedata.sort(this.different)
      this.enddata = this.beforedata.reverse().slice(0,9);
      if(this.enddata==''){
        this.loading_let= true;
      }else{
        this.loading_let= false;
      }
    },
    different(a,b){
      const TimeA = a.Time
      const TimeB = b.Time
      let comparison = 0;
      if (TimeA > TimeB) {
        comparison = 1;
      } else if (TimeA < TimeB) {
        comparison = -1;
      }
      return comparison;
    },
        nowTimes(){        
        let self = this;
        setInterval(function(){
          self.catch_d();
        }, 500);
      },
  },created(){
    this.nowTimes();
  }
}
</script>
